<template>
    <div class="drop-box-content" >
        <div class="slot-component" v-bind:class="boxHide ? 'hide' : ''">
            <div ref="drop_content">
                <slot  name="slot-component"></slot>
            </div>
        </div>

        <Poptip v-if="isTips" trigger="hover" title="Tooltip" :transfer="true" placement="right-start" content="content"
                class="poptip">
            <Icon type="ios-alert-outline" color="red" size="18"/>
            <div class="tips-con" slot="content">
                <p></p>
                <p class="meth1"></p>
                <Button class="tips-btn" type="warning" @click="onButtonShowDrawer()" size="small"></Button>
                <p></p>
                <div>
                    <p class="meth3"></p>
                    <Icon v-show="isDropDown" class="tips-drop-down" type="ios-arrow-dropdown" size="18"/>
                </div>
            </div>
        </Poptip>
        <div v-if="isDrop" class="drop-box">
            <Icon v-show="isDropUp" class="drop-up" @click="dropUp()" type="ios-arrow-dropup" size="18"/>
            <Icon v-show="isDropDown" class="drop-down" @click="dropDown()" type="ios-arrow-dropdown" size="18"/>
        </div>

    </div>
</template>

<script>
    import bus from '../../event-bus.js';
    import ResizeObserver from 'resize-observer-polyfill';

    export default {
        name: "table-ui-overflowHidden",
        pageAuthor: 'xiaosiyan',
        props: {
            'minwidth': {type: Number, default: 0},
            'maxHeight': {type: Number, default: 0},
        },
        data() {
            return {

                menuKey: 1,
                // 
                isTips: false,

                // 
                isDrop: false,
                // 
                isDropUp: false,
                // 
                isDropDown: false,
                // 
                boxHide: false,

                // 
                width: null,
                //
                height: null,
                // 
                nowWidth: null,

                maxMidHeight:58


            }
        },
        methods: {
            onButtonShowDrawer() {
                bus.$emit('show-drawer');
            },
            viewWidth() {
                let _that = this;
                const ro = new ResizeObserver((entries, observer) => {
                    for (const entry of entries) {
                        const {left, top, width, height} = entry.contentRect;
                        _that.width = width;
                        _that.height = height;
                    }
                });
                ro.observe(this.$refs.drop_content);
            },
            dropUp() {
                this.nowWidth = this.width;
                this.isDropDown = true;
                this.isDropUp = false;
                this.boxHide = true;
                this.isTips = true;

            },
            dropDown() {
                this.nowWidth = this.width;
                this.isDropUp = true;
                this.isDropDown = false;
                this.boxHide = false;
                this.isTips = false;
            },
            updateViewSize() {
                let _that = this;
                if (this.height > _that.maxMidHeight) {
                    this.isDrop = true;
                    this.isDropDown = true;
                    // 55 3
                    this.isTips = true;
                    this.boxHide = true;

                } else {
                    this.isTips = false;
                    this.isDrop = false;
                    this.isDropUp = false;
                    this.isDropDown = false;
                    this.boxHide = false;

                }


            }
        },
        mounted: function () {
            this.viewWidth();
        },
        watch: {
            width: {
                handler(nv) {

                    let _that = this;
                    // 
                    if (this.nowWidth !== nv) {


                        if (nv < _that.minwidth) {
                            if (this.height > _that.maxMidHeight) {
                                this.isDrop = true;
                                this.isDropDown = true;
                                // 55 3
                                this.isTips = true;
                                this.boxHide = true;

                            } else {
                                this.isTips = false;
                                this.isDrop = false;
                                this.isDropUp = false;
                                this.isDropDown = false;
                                this.boxHide = false;
                            }

                        } else {


                            if(+_that.maxHeight !== 0 && (_that.height > _that.maxHeight)){
                                this.isDrop = true;
                                this.isDropDown = true;
                                // 55 3
                                this.isTips = true;
                                this.boxHide = true;
                            }else{
                                this.$set(_that, 'isTips', false);
                                this.boxHide = false;
                                this.isTips = false;
                                this.isDrop = false;
                                this.isDropUp = false;
                                this.isDropDown = false;
                            }




                        }
                    }

                },
                deep: true,
                immediate: true
            }
        }
    }
</script>

<style lang="scss" scoped>
    .box-poptip {
        margin-right: 5px;
        margin-bottom: 5px;
        display: inline-block;
    }

    .tips-con {
        p {
            margin: 3px 0;
        }
    }

    .drop-box-content {
        position: relative;
        width: 100%;
        height: 100%;

        .poptip {
            position: absolute;
            right: -14px;
            top: 3px;
        }
        .drop-box {
            display: flex;
            justify-content: flex-end;
            height: 1px;
            .drop-up {
                cursor: pointer;
                position: absolute;
                right: -14px;
                bottom: 0;
                z-index: 100;

            }
            .drop-down {
                cursor: pointer;
                position: absolute;
                right: -14px;
                bottom: 0;
                z-index: 99;

            }
        }

        .slot-component {
            &.hide {
                display: block;
                cursor: pointer;
                overflow: hidden;

                height: 55px;
            }
        }
    }

    .tips-drop-down {
        display: inline-block;
        vertical-align: middle;
    }

    .tips-btn {
        vertical-align: middle;
        display: inline-block;
        margin-left: 3px;
    }

    .meth1 {
        vertical-align: middle;
        display: inline-block;
    }

    .meth3 {
        vertical-align: middle;
        display: inline-block;
    }
</style>