import TableUIImgPreView from '../compontents/table-ui-imgPreView.vue';
import TableUIImgView from '../compontents/table-ui-imgView.vue';
import TableUIButton from '../compontents/table-ui-button.vue';
import TableUIButtonGroup from '../compontents/table-ui-buttonGroup.vue';
import TableUITime from '../compontents/table-ui-time.vue';

import TableUILink from '../compontents/table-ui-link.vue';
import TableUIDom from '../compontents/table-ui-dom.vue';
import TableUIDropDown from '../compontents/table-ui-dropDown.vue';
import TableUISpan from '../compontents/table-ui-span.vue'
import TableUITags from '../compontents/table-ui-tags.vue'

export default {
    components: {
        TableUIImgPreView,
        TableUIImgView,
        TableUIButtonGroup,
        TableUITime,
        TableUILink,
        TableUIDom,
        TableUIDropDown,
        TableUISpan,
        TableUITags,
        TableUIButton
    },
    methods: {
        //TableUI
        setColumnsConifg: function (columns) {
            /**
             * 
             * 1. iviewwatcherbug
             * 2. UIrender
             * 3. UIlevel
             * */
            let self = this;
            let columnsTemp = [];
            if (columns && columns.length > 0) {

                //watch
                columns.forEach(function (item) {
                    columnsTemp.push(Object.assign({}, item));
                });


                columnsTemp.forEach(function (item, index) {
                    if (item.key) {
                        if (!item.level) {
                            item.level = 2;                     //m--level
                            if (item.UI && item.UI.type) {
                                switch (item.UI.type) {
                                    case 'button':
                                    case 'buttonGroup': {
                                        item.level = 1;         //UI
                                    }
                                }
                            }
                        }
                    } else {
                        item.level = 1;                          //key
                    }


                    if (item.UI && item.UI.type) {
                        let {UI, key} = item;
                        //

                        item.render = function (h, params) {
                            let value = params.row[key];

                            switch (item.UI.type) {
                                case 'img': {
                                    return h(TableUIImgView, {
                                            props: {
                                                src: value,
                                                className: UI['class']
                                            }
                                        }
                                    )
                                }
                                case 'time': {
                                    console.log('debugger--time');
                                    return h(TableUITime, {
                                        props: Object.assign({
                                            time: value,
                                            unit: UI['unit'],
                                            needBr: UI['needBr']
                                        })
                                    });
                                }
                                case 'link': {
                                    return h(TableUILink, {
                                        props: Object.assign({
                                            UI: {
                                                text: UI['text'] ? UI['text'] : value,
                                                prefHref: UI['preHref'] ? UI['preHref'] : '',
                                                needTransform: UI.needTransform  // UI--
                                            },
                                            setKey: key,                    // key
                                            setTransform: self.transform,   // ""
                                            setParams: params,              // ''keyvalue
                                        })
                                    });

                                }
                                case 'button': {
                                    if (value && key) {
                                        //
                                        return h(TableUIButton, {
                                            props: {
                                                config: UI,
                                                button: value
                                            },
                                            on: {
                                                click: (name) => {
                                                    self.onRowClick(name, params.row, params.index, self.Table_clickArgs)
                                                }
                                            }
                                        });

                                    } else {
                                        break;
                                    }
                                }
                                case 'buttonGroup': {
                                    return h(TableUIButtonGroup, {
                                        props: {
                                            vertical: UI.vertical,
                                            list: UI.list,
                                            buttons: value
                                        },
                                        on: {
                                            click: (name) => {
                                                self.onRowClick(name, params.row, params.index, self.Table_clickArgs)
                                            }
                                        }
                                    });
                                }
                                case 'tags': {
                                    if (value && value.length > 0) {
                                        return h(TableUITags, Object.assign({
                                                props: {
                                                    info: value,                  // value
                                                    UI: {
                                                        needTransform: UI.needTransform  // UI--
                                                    },
                                                    setKey: key,                  // key
                                                    setTransform: self.transform, // ""
                                                    setParams: params             // ''keyvalue
                                                }
                                            })
                                        )
                                    } else {
                                        break;
                                    }
                                }

                                //
                                case 'span': {
                                    if (value) {
                                        return h(TableUISpan, Object.assign({
                                                props: {
                                                    UI: {
                                                        style: UI.style,                 // UI--
                                                        text: UI.text,                   // UI--
                                                        type: UI.type,                   // UI--
                                                        minWidth: UI.minWidth || 150,    // UI-- 150
                                                        maxHeight: UI.maxHeight || 0,
                                                        needTransform: UI.needTransform  // UI--
                                                    },
                                                    styleInfo: {                     // 
                                                        style: value.style || '',           // --
                                                        text: value.text,             // --
                                                        value: value                  // --
                                                    },
                                                    setKey: key,                    // key
                                                    setTransform: self.transform,   // ""
                                                    setParams: params,              // ''keyvalue
                                                },

                                            })
                                        )
                                    } else {
                                        return h(TableUISpan, Object.assign({
                                                props: {
                                                    UI: {
                                                        style: UI.style,                 // UI--
                                                        text: UI.text,                   // UI--
                                                        type: UI.type,                   // UI--
                                                        minWidth: UI.minWidth || 150,    // UI-- 150
                                                        needTransform: UI.needTransform  // UI--
                                                    },
                                                    setKey: key,                    // key
                                                    setTransform: self.transform,   // ""
                                                    setParams: params,              // ''keyvalue
                                                },

                                            })
                                        )
                                    }

                                }

                                //
                                case 'dropDown': {
                                    if (value && key) {
                                        return h(TableUIDropDown, Object.assign({
                                            props: {
                                                UI: {
                                                    list: UI.list,                   // UI-- 
                                                    btnText: UI.btnText || '', // UI-- 
                                                    btnType: UI.btnType,             // UI-- 
                                                    iconType: UI.type || 'default',  // UI-- icon 
                                                },
                                                button: value
                                            },
                                            on: {
                                                'on-click': (name) => {
                                                    self.onRowClick(name, params.row, params.index, self.Table_clickArgs);
                                                }
                                            }
                                        }))
                                    }
                                    break;
                                }

                                //---mo
                                case 'dom': {
                                    return h(TableUIDom, {
                                            props: {
                                                UI: {
                                                    data_name: UI['name'] ? UI['name'] : 'table-ui-dom',
                                                },
                                                data_value: value
                                            }
                                        }
                                    )

                                }
                                //
                                case 'imgPreView': {
                                    return h(TableUIImgPreView, {
                                        props: {
                                            imgPreView: value,
                                        }
                                    });
                                }

                                case 'topicContent': {
                                    return self.$loadTableUIComponents(h, {
                                        type: item.UI.type,
                                        value,
                                        UI
                                    });
                                }

                                default: {
                                    return self.$loadTableUIComponents(h, {
                                        type: item.UI.type,
                                        value,
                                        UI
                                    });
                                }
                            }
                        };

                        //
                        item.minWidth = (() => {
                            //
                            if (!item.minWidth) {
                                switch (item.UI.type) {
                                    case 'imgPreView': {
                                        return 100;
                                    }
                                    case 'action': {
                                        return 100
                                    }
                                }
                            } else {
                                return item.minWidth;
                            }

                        })();
                    }
                });

                return columnsTemp;
            } else {
                return [];
            }

        },
    }
}