<template>
    <div class="display-flex">
        <div class="flex-left">
            <Upload action="/General/Upload/Picture/upload"
                    :with-credentials="true"
                    :show-upload-list="true"
                    :max-size="4096"
                    :multiple="false"
                    :format="['jpg','jpeg','png','gif']"
                    :on-success="handleSuccess"
                    :on-error="handleError"
                    :on-format-error="handleFormatError"
                    :on-exceeded-size="handleMaxSize"
                    :on-remove="handleRemove"
                    :before-upload="handleBeforeUpload"
                    :default-file-list="imgUploadList"
                    ref="imgUploader"
            >
                <Button icon="ios-cloud-upload-outline"></Button>
                <div>
                    {{imgTitle}}
                    <strong style="display: block" v-if="limitWidth">:{{limitWidth}}</strong>
                    <strong style="display: block" v-if="limitHeight">:{{limitHeight}}</strong>
                </div>

            </Upload>
        </div>
        <div class="flex-right">
            <Poptip v-if="imgPreView" placement="right-start" trigger="hover">
                <div class="img-box"
                     :style="{backgroundImage: 'url(' + imgPreView+ ')'}"></div>
                <div slot="content" class="img-view"
                     :style="{backgroundImage: 'url(' + imgPreView+ ')'}">
                </div>
            </Poptip>
        </div>
    </div>
</template>

<script>
    export default {
        name: "imgUploader",
        pageAuthor: 'xiaosiyan',
        props: {
            limitWidth: {type: Number, default: 0},
            limitHeight: {type: Number, default: 0},
            imgTitle: {type: String, default: ''},
            allData: {type: String, default: ''}
        },
        data() {
            return {
                //
                imgPreView: '',
                //
                imgUploadList: []
            }
        },
        model: {
            prop: 'allData',
            event: 'update'
        },
        methods: {
            handleSuccess: function (response, file, fileList) {
                if (response.status == 2) {
                    let check = true;
                    //
                    if (this.limitWidth === 0 && this.limitHeight === 0) {
                        check = true;
                    } else {
                        //
                        if (this.limitWidth && this.limitWidth !== response.data.width) {
                            check = false;
                        }
                        // 
                        if (this.limitHeight && this.limitHeight !== response.data.height) {
                            check = false;
                        }
                    }

                    if (check) {
                        this.imgPreView = response.data.url;
                        this.$emit('update', response.data.url);
                    } else {
                        this.imgPreView = '';
                        this.$emit('update', '');
                        this.$Message.error('!');
                        this.imgUploadList = [];
                    }
                } else {
                    this.$Message.error('!')
                }
            },
            handleFormatError: function () {
                this.$Message.error('!');
            },
            handleError(res, file) {
                this.$Message.error('!')
            },
            handleMaxSize(file) {
                this.$Notice.warning({
                    title: '',
                    desc: '  ' + file.name + ' ,  4M '
                });
            },
            handleRemove(file, fileList) {
                // 
                this.imgPreView = '';
                this.$emit('update', '');
            },
            handleBeforeUpload() {
                this.$refs.imgUploader.clearFiles();
            }
        },
        watch: {
            allData: {
                handler: function (nv) {

                    //this.imgPreView = nv;

                    // mo 
                    //  
                    if(nv){
                        this.imgPreView = nv;
                    }else{
                        if(this.$refs.imgUploader){
                            this.imgPreView = [];
                            this.handleBeforeUpload()
                        }
                    }

                },
                deep: false,
                immediate: true
            }
        }
    }
</script>

<style lang="scss" scoped>
    .img-box {
        width: 100px;
        height: 100px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        border-radius: 10px;
    }

    .img-view {
        width: 400px;
        height: 400px;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
    }

    .display-flex {
        display: flex;
    }

    .flex-left {
        margin-right: 20px;
        width: 120px;
        text-overflow: ellipsis;
    }
</style>